import React from 'react';
import { useState } from 'react';
import ReactDOM from 'react-dom';
import './App.css';

var expression = '';
var ans = '';
function calculate(){
  ans = eval(expression);
  //document.getElementById('result').value = ans;
  expression = ans;
  //console.log(ans);
  return ans;

}

const exp = (e)=>{
 
  expression += e;
  document.getElementById('result').innerHTML = expression;
  //console.log(expression);
  

}
// function signchange(){
//   //var res = parseInt(expression);
//     ans = ans*(-1);
//     console.log(ans);
//     document.getElementById('result').value = ans;
  
// }
 function Calculator(){

  const[ans,setAns] = useState(0);
  const clear = ()=>{
    setAns(0);
    expression = '';
    //setAns(0);
    //console.log(expression);
  
  }
  function signChange(){
    setAns(ans*(-1));
    var x = parseInt(expression);
    if(x>0){
    expression = '-'+expression;
    }
  }

    return(
      <div className='cal-container'>
          <div className='span-full'  id = 'result'>{ans}</div>
          <button className='btn' onClick={clear}>AC</button>
          <button className='btn' onClick={signChange}>+/-</button>
          <button className='btn'  onClick={()=>exp('%')}>%</button>
          <button className='btn2' onClick={()=> exp('/')}>รท</button>
          <button className='btn' onClick={()=> exp(7)}>7</button>
          <button className='btn' onClick={()=> exp(8)}>8</button>
          <button className='btn' onClick={()=> exp(9)}>9</button>
          <button className='btn2' onClick={()=> exp('*')}>ร</button>
          <button className='btn'  onClick={()=> exp(4)}>4</button>
          <button className='btn'  onClick={()=> exp(5)}>5</button>
          <button className='btn'  onClick={()=> exp(6)}>6</button>
          <button className='btn2'  onClick={()=> exp('-')}>-</button>
          <button className='btn'  onClick={()=> exp(1)}>1</button>
          <button className='btn'  onClick={()=> exp(2)}>2</button>
          <button className='btn'  onClick={()=> exp(3)}>3</button>
          <button className='btn2'  onClick={()=> exp('+')}>+</button>
          <button className='span-two'  onClick={()=> exp(0)}>0</button>
          <button className='btn'  onClick={()=> exp('.')}>.</button>
          <button className='btn2' onClick={()=>setAns(calculate(expression))}>=</button>
          
        
      </div>
      
    );

  }
  ReactDOM.render(<Calculator/>,document.getElementById('root'));


